<template>
	<li class="item" @click="handleDelete">{{content}}</li>
</template>

<script>
export default {
	props: ['content','index'],
	methods: {
		handleDelete: function() {
			this.$emit('deleteone',this.index)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.item{
	color: green;
}
</style>



